import "./FrameHeader.css";

export interface IFrameHeaderProps {
    text: string;
    onClick?: ()=> void;
}

export const FrameHeader = (props: IFrameHeaderProps) => {
    return (
        <div className="frame-header">
            <div onClick={props.onClick} className="frame-header-icon"></div>
            <div className="frame-header-text">{props.text}</div>
        </div>
    )
}