import { useState } from "react";
import { useDispatch } from "react-redux";
import { Button } from "../../Components/Button/Button";
import { ItemList } from "../../Components/ItemList/ItemList";
import { Todo } from "../../reducers/newSlice";
import "./TodoList.css"
import { generateGuid } from "../../Utils/GenerateGuid";
import { Modal } from "../../Components/Modal/Modal";
import { AddTaskModal } from "../../Components/AddTaskModal/AddTaskModal";

export interface ITodoListProps {
    items: Todo[];
    title: string;
    addFunction: (item: Todo) => any;
    deleteFunction: (item: Todo) => any;
    changeDoneFunction?: (item: Todo) => any;
    deleteTodosFunction?: (items: Todo[]) => any;
    setTodosFunction?: (items: Todo[]) => any;
    editFunction: (items: Todo) => any;
}

export const TodoList = (props: ITodoListProps) => {
    const { items, title, addFunction, deleteFunction, changeDoneFunction, deleteTodosFunction, setTodosFunction, editFunction } = props; 
    const [addModalOpen, setAddModalOpen] = useState<boolean>(false);
    const [editModalOpen, setEditModalOpen] = useState<boolean>(false);
    const [inputValue, setInputValue] = useState<string>("");
    const dispatch = useDispatch()
    const [currentTask, setCurrentTask] = useState<Todo | null>(null);

    const onDragStartHandler = (e: React.DragEvent, task: Todo) => {
        setCurrentTask(task);
    }

    const onDropHandler = (e: React.DragEvent, task: Todo) => {
        if(!setTodosFunction) return;
        const todos = items.map(el => {
            if(el.id === task.id && currentTask!== null) {
                return {...el, order: currentTask.order}
            }
            if(el.id === currentTask?.id && currentTask!== null) {
                return {...el, order: task.order}
            }
            return el;
        })
        dispatch(setTodosFunction(todos.sort(sortCards)))
        
    }

    const sortCards = (a: Todo,b:Todo) => {
        if (a.order > b.order) {
            return 1
        } else {
            return -1
        }
   } 

    const addItem = () => {
        if (inputValue) {
            const task = {
                id: generateGuid(),
                title: inputValue,
                done: false,
                order: items.length
            }
            dispatch(addFunction(task));
        }
        setInputValue("");
        setAddModalOpen(false);
    }

    const deleteItem = (task: Todo) => {
        dispatch(deleteFunction(task))
    }

    const editItem = () => {
        if (inputValue && currentTask) {
            const editTask = {
                id: currentTask.id,
                title: inputValue,
                done: currentTask.done,
                order: currentTask.order
            }
            dispatch(editFunction(editTask))
            setInputValue("");
            setEditModalOpen(false);
        }
       
        
    }

    const deleteItems = () => {
        if (!deleteTodosFunction) return;
        const deletedItems = items.filter(item => item.done);
        if (deletedItems && deletedItems.length) {
            dispatch(deleteTodosFunction(deletedItems))
        }
    }

    const changeDone = (task: Todo) => {
        if (!changeDoneFunction) return;
        dispatch(changeDoneFunction(task))
    }

    const onCancel = () => {
        setInputValue("");
        setAddModalOpen(false);
        setEditModalOpen(false);
    }

    

    return (
        <>
            <div className="todo-list">
                <div className={"todo-list-header"}>
                    <h1 className={"todo-list-header-text"}>{title}</h1>
                    <div className={"todo-list__buttons-block"}>
                        <Button text={"Добавить задачу"} iconClassName={"add-button-icon"} onClick={() => setAddModalOpen(true)}></Button>
                        <Button text={"Удалить выполненные"} iconClassName={"delete-button-icon"} onClick={deleteItems}></Button>
                    </div>
                    
                </div>
                {items.map((item) => 
                    <ItemList changeDone={changeDone} deleteItem={deleteItem} key={item.id} task={item} onDragStartHandler={onDragStartHandler} onDropHandler={onDropHandler} editItem={()=> {setEditModalOpen(true); setInputValue(item.title); setCurrentTask(item)}}/>
                )}
            </div>
            <AddTaskModal open={addModalOpen} setOpen={setAddModalOpen} inputValue={inputValue} setInputValue={setInputValue} addItem={addItem} onCancel={onCancel} />
            <AddTaskModal open={editModalOpen} setOpen={setEditModalOpen} inputValue={inputValue} setInputValue={setInputValue} addItem={editItem} onCancel={onCancel} />
        </>
        
    )
}