import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { TodoList } from './Pages/TodoList/TodoList';
import { ItemList, tableData } from './Data/Data';
import { SidebarPanel } from './Components/SidebarPanel/SidebarPanel';
import { Table } from './Pages/Table/Table';
import { FrameHeader } from './Components/FrameHeader/FrameHeader';
import { Calendar } from './Pages/Calendar/Calendar';
import { useSelector } from 'react-redux';
import { RootState } from './reducers';
import { addEvent, addTodo, changeDone, deleteEvent, deleteTodo, deleteTodos, setTodos, Todo, editTodo } from './reducers/newSlice';
import { generateGuid } from './Utils/GenerateGuid';
import moment from 'moment';

export enum Pages {
  TodoList,
  Table,
  Week,
  Today,
  Calendar
}

function App() {
  const [selectedPage, setSelectedPage] = useState(Number(window.localStorage.getItem("selectedPage")) || Pages.TodoList);
  const [isOpen, setOpen] = useState(false);
  const onClickMenuIcon = () => {
    setOpen(!isOpen)
  }

  const todos = useSelector((state: RootState) => state.todos.todos);
  const events = useSelector((state: RootState) => state.todos.events);
  const todayEvents = events.filter(event => event.start.toDateString() === (new Date()).toDateString()).map((event, index) => {
    return {
      id: generateGuid(),
      order: index,
      title: event.title,
      done: false
    }
  });
  const addTodayEvent = (task: Todo) => {
    const event = {
      id: generateGuid(),
      title: task.title,
      start: moment().startOf('day').toDate(),
      order: 0
    }
    return addEvent(event);
  }

  const deleteTodayEvent = (task: Todo) => {
    const event = {
      title: task.title,
      start: moment().startOf('day').toDate(),
      order: task.order
    }
    return deleteEvent(event);
  }

  const renderPage = ( selectedPage: Pages) => {
    
    switch (selectedPage) {
      case Pages.TodoList:
        return <TodoList items={todos} title='Todo list' addFunction={addTodo} deleteFunction={deleteTodo} changeDoneFunction={changeDone} deleteTodosFunction={deleteTodos} setTodosFunction={setTodos} editFunction={editTodo}></TodoList>
      case Pages.Table:
        return <Table data={tableData}></Table>
      case Pages.Week:
          return <Calendar key="week" initialView='dayGridWeek'></Calendar>
      case Pages.Today:
          return <TodoList items={todayEvents} title={(new Date()).toLocaleDateString()} addFunction={addTodayEvent} deleteFunction={deleteTodayEvent} changeDoneFunction={changeDone} deleteTodosFunction={deleteTodos} setTodosFunction={setTodos} editFunction={editTodo}></TodoList>
      case Pages.Calendar:
          return <Calendar  key="month" initialView='dayGridMonth'></Calendar>
      default: new Error("Нет такой страницы")
    }
      
  }

  const goToPage = (selectedPage: number) => {
    setSelectedPage(selectedPage);
    window.localStorage.setItem("selectedPage", selectedPage.toString())
    setTimeout(() => {
      setOpen(false);
    }, 100)
    
  }

  return (
    <>
      <FrameHeader onClick={onClickMenuIcon} text="Perfect planer"></FrameHeader>
      <div className="container">
        <SidebarPanel isOpen={isOpen} selectedPage={selectedPage} setSelectedPage={goToPage}></SidebarPanel>
        <div className="body">
          {renderPage(selectedPage)}
        </div>
        
      </div>
    </>
    
  );
}

export default App;
