import { useState } from "react";
import { Button } from "../Button/Button";
import { ColumnItem } from "../ColumnItem/ColumnItem";
import "./Column.css";

export interface IColumnItem {
    order: number;
    name: string;
    tasks: Array<string>;
}

export interface IColumnProps {
    data: IColumnItem ;
    deleteColumn: (name: string) => void;
    onDragStartHandler: (e:  React.DragEvent, column: IColumnItem) => void;
    onDropHandler: (e:  React.DragEvent, column: IColumnItem) => void;
}

export const Column = (props: IColumnProps) => {
    const {data, deleteColumn, onDragStartHandler, onDropHandler} = props;
    const [items, setItems] = useState(props.data.tasks);
    const onClick = () => {
        const taskName = window.prompt("Enter name of task")
        taskName && setItems([...items, taskName])
    }

    const deleteItem = (el: string) => {
        setItems(items.filter(elem => elem !== el));
    }
    
    

    const onDragEndHandler = (e: React.DragEvent) => {
        (e.target as HTMLElement).style.background = 'white'
    }

    const onDragLeaveHandler = (e: React.DragEvent) => {
        (e.target as HTMLElement).style.background = 'white'
    }

    const onDragOverHandler = (e: React.DragEvent) => {
        e.preventDefault();
        (e.target as HTMLElement).style.background = 'lightgray'
    }

    

    return (
        <div className="column" draggable={true} onDragStart={(e) => onDragStartHandler(e, data)} onDragEnd={(e) => onDragEndHandler(e)} onDragLeave={(e) => onDragLeaveHandler(e)} onDragOver={(e) => onDragOverHandler(e)} onDrop={(e) => onDropHandler(e, data)}>
            <div className="column-header">{props.data.name}</div>
            <button onClick={() => deleteColumn(props.data.name)} className="column-delete-button"></button>
            <Button onClick={onClick} text={"Добавить задачу"}></Button>
            {items?.map(el => {
                return (
                    <ColumnItem name={el} onClick={deleteItem}></ColumnItem>
                )
            })}
        </div>
    )
}