import classNames from "classnames";
import { useEffect } from "react";
import { Pages } from "../../App";
import "./SidebarPanel.css";

export const SidebarPanel = (props: {setSelectedPage: any, isOpen: boolean, selectedPage: number}) => {

    return (
        <div className={props.isOpen ? "sidebar-panel" : "sidebar-panel  hide"}>
            <button className={classNames("sidebar-panel-button", props.selectedPage === 0 && "select")} onClick={() => props.setSelectedPage(Pages.TodoList)}>ToDo List</button>
            <button className={classNames("sidebar-panel-button", props.selectedPage === 1 && "select")} onClick={() => props.setSelectedPage(Pages.Table)}>Custom desk</button>
            <button className={classNames("sidebar-panel-button", props.selectedPage === 2 && "select")} onClick={() => props.setSelectedPage(Pages.Week)}>Week</button>
            <button className={classNames("sidebar-panel-button", props.selectedPage === 3 && "select")} onClick={() => props.setSelectedPage(Pages.Today)}>Today</button>
            <button className={classNames("sidebar-panel-button", props.selectedPage === 4 && "select")} onClick={() => props.setSelectedPage(Pages.Calendar)}>Calendar</button>
        </div>
    )
}