import { useState } from "react";
import { Todo } from "../../reducers/newSlice";
import "./ItemList.css"

export interface IItemListProps {
    task: Todo;
    deleteItem: (task: Todo) => void;
    changeDone: (task:Todo) => void;
    onDragStartHandler: (e: any, task:Todo) => void;
    onDropHandler: (e: any, task:Todo) => void;
    editItem: any
}

export const ItemList = (props: IItemListProps) => {
    const { task, changeDone, editItem } = props;

    const onDragOverHandler = (e: React.DragEvent) => {
        e.preventDefault();
    }

    const onMouseDown = (e: any) => {
        e.stopPropagation();
        e.preventDefault();
    }
    
    return <div  className={task.done ? "item-list done" : "item-list"}  draggable={true} 
    onDragStart={(e) => props.onDragStartHandler(e, task)} onDrop={(e) => props.onDropHandler(e, task)} onDragOver={onDragOverHandler }>
            <div className="item-list__title">
                <button onClick={() => changeDone(task)} onMouseDown={(e) =>onMouseDown(e)} className="item-list_done-button"></button>
                <div className="item-list_name">{task.title}</div>
            </div>
            <div className="item-list_buttons">
                <button onClick={editItem} onMouseDown={(e) =>onMouseDown(e)} className="item-list_edit-button"></button>
                <button onClick={() => props.deleteItem(task)} onMouseDown={(e) =>onMouseDown(e)} className="item-list_delete-button"></button>
            </div>
            
        </div>
}