import { useState } from "react";
import { Button } from "../../Components/Button/Button";
import { Column, IColumnItem } from "../../Components/Column/Column";
import "./Table.css";

export interface ITableProps {
    data: Array<IColumnItem>;
}

export const Table = (props: ITableProps) => {
    const [data, setData] = useState(props.data);
    const [currentColumn, setCurrentColumn] = useState<IColumnItem | null>(null);

    const onDragStartHandler = (e: React.DragEvent, column: any) => {
        setCurrentColumn(column);
    }

    const addColumn = () => {
        const columnName = window.prompt("Enter name of task")
        const elem = {name: columnName, tasks: [], order: data.length} as IColumnItem;
        setData([...data, elem])
    }

    const onDropHandler = (e: React.DragEvent, column: IColumnItem) => {
        (e.target as HTMLElement).style.background = 'white'
        setData(data.map(col => {
            if(col.name === column.name && currentColumn!== null) {
                return {...col, order: currentColumn.order}
            }
            if(col.name === currentColumn?.name && currentColumn!== null) {
                return {...col, order: column.order}
            }
            return col;
        }))
        
    }

    const sortCards = (a: IColumnItem,b:IColumnItem) => {
        if (a.order > b.order) {
            return 1
        } else {
            return -1
        }
   } 

    const deleteColumn = (name: string) => {
        setData(data.filter(el => el.name !== name))
    }
    
    return (
        <div className="table">
            <div className="table-header">
                <span className="table-header__text">Таблица</span>
                <Button onClick={addColumn} text={"Добавить столбец"}></Button>
            </div>
            <div className="table-body">
                {data.sort(sortCards).map(item => {
                return <Column data={item} deleteColumn={deleteColumn} onDragStartHandler={onDragStartHandler} onDropHandler={onDropHandler}></Column>
                })}
            </div>
            
        </div>
    )
}